import { Button, Card, Form, InputNumber, message, Radio } from "antd";
import React from "react";
import { useNavigate } from "react-router-dom";
import { post } from "../../utils";
import Back from "../back/Back";
import "./MakeOrder.css";

function Order() {
  const navigate = useNavigate();
  const key = "loading";

  function onFinish(values: {
    chargeQuantity: number;
    chargeType: "fast" | "slow";
  }) {
    message.loading({ content: "...", duration: 0, key });
    post("userSendOrder", values)
      .then(() => {
        message.success({ content: "", key });
        navigate(-1);
      })
      .catch(e => {
        message.error({ content: e.message, key });
      });
  }

  return (
    <>
      <Back title=""></Back>
      <Card>
        <Form layout="vertical" onFinish={onFinish}>
          <Form.Item
            label=""
            name="chargeType"
            rules={[{ required: true, message: "" }]}
          >
            <Radio.Group>
              <Radio value="fast"></Radio>
              <Radio value="slow"></Radio>
            </Radio.Group>
          </Form.Item>

          <Form.Item
            label=""
            name="chargeQuantity"
            rules={[
              { required: true, message: "" },
              {
                type: "number",
                min: 1,
                message: "1",
              },
              {
                type: "number",
                max: 100,
                message: "100",
              },
            ]}
          >
            <InputNumber precision={0} addonAfter="kWH" />
          </Form.Item>
          <Form.Item>
            <Button type="primary" htmlType="submit">
              
            </Button>
          </Form.Item>
        </Form>
      </Card>
    </>
  );
}

export default Order;
